@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
*,*::before,*::after{margin:0;padding:0;box-sizing:border-box}::-webkit-scrollbar{width:.75em}::-webkit-scrollbar-track{background-color:#b3b3b3}::-webkit-scrollbar-thumb{background-color:#4d4d4d;border:2px solid #b3b3b3}::-webkit-scrollbar-thumb:hover{background-color:rgba(191,64,64,.8)}@supports(scrollbar-color: #4d4d4d #b3b3b3){*{scrollbar-color:#4d4d4d #b3b3b3;scrollbar-width:thin}}.container{--border-radius: 5px;--weight-regular: 300;--weight-semibold: 600;--weight-bold: 800;--fsize-big: 1rem;--fsize-small: .875rem;font-family:"Nunito Sans",sans-serif;color:var(--text-clr);background-color:var(--background-clr);min-height:100vh;transition:color 100ms linear,background-color 150ms linear}.container.light-theme{--text-clr: hsl(200, 15%, 8%);--background-clr: hsl(0, 0%, 98%);--elements-clr: hsl(0, 0%, 100%);--shadow: 2px 4px 4px hsla(0, 0%, 90%, .15), 6px 12px 12px hsla(0, 0%, 90%, .1);--btn-shadow: 1px 2px 2px hsla(0, 0%, 90%, .15), 3px 6px 6px hsla(0, 0%, 90%, .1), 6px 12px 12px hsla(0, 0%, 90%, .1)}.container.dark-theme{--text-clr: hsl(0, 0%, 100%);--background-clr: hsl(207, 26%, 17%);--elements-clr: hsl(209, 23%, 22%);--shadow: 2px 4px 4px hsla(207, 26%, 10%, .15), 6px 12px 12px hsla(207, 26%, 10%, .1);--btn-shadow: 1px 2px 2px hsla(207, 26%, 10%, .2), 3px 6px 6px hsla(207, 26%, 10%, .15), 6px 12px 12px hsla(207, 26%, 10%, .15)}button{color:var(--text-clr);cursor:pointer}a{display:inline-block;text-decoration:none;color:currentColor}img{max-width:100%}ul{list-style:none}.wrapper{width:min(85%,1440px);margin:0 auto}.highlighted{font-weight:var(--weight-semibold)}.btn{padding:.4375em 1.75em;border:none;box-shadow:var(--btn-shadow);background-color:var(--elements-clr);color:var(--text-clr);font-size:var(--fsize-small);transition:opacity 150ms linear}.btn:hover{opacity:.7}
.header{position:fixed;left:0;top:0;right:0;z-index:999;padding:1.5em .625em;box-shadow:var(--shadow);background-color:var(--elements-clr)}.header .wrapper{display:flex;justify-content:space-between}.header__logo{font-weight:var(--weight-bold)}.header__theme-switch{border:none;background-color:transparent;font-size:var(--fsize-small);font-weight:var(--weight-semibold)}.header__theme-switch svg{margin-right:.25em}.header+*{padding-top:6em}
.countries-list{padding-bottom:2em}.countries-list__menu{display:flex;flex-wrap:wrap;justify-content:space-between;grid-gap:1.875em;gap:1.875em}.countries-list__content{margin-top:1.875em;display:flex;flex-wrap:wrap;justify-content:center;grid-gap:2.9375em;gap:2.9375em}
.search-input{display:flex;width:min(100%,500px);padding:1.25em 1.5em;border-radius:var(--border-radius);background-color:var(--elements-clr);box-shadow:var(--shadow)}.search-input #input{width:100%;margin-left:1.5em;background-color:transparent;border:none;color:var(--text-clr);font-size:var(--fsize-small);outline-width:0}.search-input #input::-webkit-input-placeholder{font-family:"Nunito Sans",sans-serif;color:var(--text-clr)}.search-input #input:-ms-input-placeholder{font-family:"Nunito Sans",sans-serif;color:var(--text-clr)}.search-input #input::placeholder{font-family:"Nunito Sans",sans-serif;color:var(--text-clr)}
.dropdown{position:relative;width:-webkit-max-content;width:max-content;height:-webkit-max-content;height:max-content;padding:1em 1.5em;background-color:var(--elements-clr);border-radius:var(--border-radius);box-shadow:var(--shadow);font-size:var(--fsize-small);cursor:pointer}.dropdown__text{min-width:140px;display:flex;justify-content:space-between}.dropdown .icon{transition:-webkit-transform 250ms ease-in-out;transition:transform 250ms ease-in-out;transition:transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out}.dropdown__list{position:absolute;top:105%;left:0;visibility:hidden;width:100%;padding:1em 1.5em;border-radius:var(--border-radius);list-style:none;background-color:var(--elements-clr);-webkit-transform:translateY(-50%) scaleY(0);transform:translateY(-50%) scaleY(0);opacity:0;transition:opacity 250ms linear,-webkit-transform 250ms ease-in-out;transition:opacity 250ms linear,transform 250ms ease-in-out;transition:opacity 250ms linear,transform 250ms ease-in-out,-webkit-transform 250ms ease-in-out}.dropdown__item{display:block;letter-spacing:.5px}.dropdown__item+.dropdown__item{margin-top:.5em}.dropdown.active .dropdown__list{visibility:visible;-webkit-transform:translateY(0) scaleY(1);transform:translateY(0) scaleY(1);opacity:1}.dropdown.active .icon{-webkit-transform:rotate(180deg);transform:rotate(180deg)}
.country-card{display:flex;flex-direction:column;justify-content:space-between;max-width:250px;border-radius:var(--border-radius);background-color:var(--elements-clr);overflow:hidden;box-shadow:var(--shadow)}@media(min-width: 625px){.country-card img{width:250px;height:166px}}.country-card__informations{padding:1.5em}.country-card__informations .country-card__name{font-size:var(--fsize-big);font-weight:var(--weight-bold);margin-bottom:1em}.country-card__informations .country-card__information{display:block;font-size:var(--fsize-small)}.country-card__informations .country-card__information+.country-card__information{margin-top:.5em}.country-card .loader__container{width:250px;height:166px}
.loader__container{display:grid;place-items:center;width:100%;height:100%}.loader__container .loader{display:block;width:50%;height:20%;opacity:.5;-webkit-animation:pulse 1s linear alternate infinite,spin 750ms linear infinite;animation:pulse 1s linear alternate infinite,spin 750ms linear infinite}@-webkit-keyframes spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@-webkit-keyframes pulse{from{opacity:.5}to{opacity:0}}@keyframes pulse{from{opacity:.5}to{opacity:0}}
.country-details{--spacing: 2.5em;padding-top:7em;padding-bottom:var(--spacing)}.country-details .icon{display:inline-block;margin-right:1em}.country-details img{display:block;box-shadow:var(--shadow);margin:0 auto}.country-details .col,.country-details__borders{display:flex;flex-wrap:wrap;grid-gap:var(--spacing);gap:var(--spacing)}.country-details__buttons{display:flex;flex-wrap:wrap;grid-gap:1em;gap:1em}.country-details__item+.country-details__item{margin-top:1em}.country-details__flag,.country-details__informations,.country-details__borders,.country-details .col{margin-top:var(--spacing)}@media(min-width: 1100px){.country-details .row{margin-top:var(--spacing);display:flex;justify-content:space-between}.country-details__flag{width:40%;margin:0}.country-details__informations{width:55%;margin:0}.country-details .row{margin-top:5em}.country-details .col{max-width:80%;justify-content:space-between}}
